import React from 'react';
import styled from 'styled-components';
import { COLORS } from 'styles/Theme';
import TextArea from 'components/common/TextArea';
import Button from 'components/common/Button';
import MessageIcon from 'components/icon/MessageIcon';
import TextBox from 'components/common/TextBox';

const Container = styled.div`
  & > textarea {
    margin-bottom: 14px;
  }
`;

const TitleWrapper = styled.div`
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 12px;

  & > svg {
    margin-right: 6px;
  }
`;

const Notification = styled.ul`
  list-style: none;
  & > li {
    margin-bottom: 10px;
    line-height: 26px;
  }
  & > li:last-child {
    margin-bottom: 36px;
  }
`;

const Bottom = () => {
  const handleButton = () => {};
  const handleChange = () => {};
  return (
    <Container>
      <TitleWrapper>
        <MessageIcon size={18} color={COLORS.primary} strokeWidth={0.2} />
        <TextBox text="       !" color={COLORS.gray_04} />
      </TitleWrapper>
      <TextArea
        placeholder="      !"
        height={138}
        onChange={handleChange}
      />
      <Notification>
        <li>
          <TextBox
            text="  ,  ."
            color={COLORS.gray_04}
            fontWeight={400}
          />
        </li>
        <li>
          <TextBox
            text=" ,    
      ."
            color={COLORS.gray_04}
            fontWeight={400}
          />
        </li>
        <li>
          <TextBox
            text="   20 ,   ."
            color={COLORS.gray_04}
            fontWeight={400}
          />
        </li>
        <li>
          <TextBox
            text="      !"
            color={COLORS.gray_04}
            fontWeight={400}
          />
        </li>
      </Notification>
      <Button
        size={100}
        text=""
        fill={COLORS.black}
        border={COLORS.black}
        textColor={COLORS.white}
        onClick={handleButton}
      />
    </Container>
  );
};

export default Bottom;
