import React from 'react';
import Tag from 'components/common/Tag';
import styled from 'styled-components';
import { COLORS } from 'styles/Theme';

const Container = styled.div``;

const Text = styled.div`
  font-size: 16px;
  line-height: 23px;
  color: ${COLORS.gray_05};

  /*  padding   */
  padding-left: 10px;
  padding-right: 9px;

  margin-bottom: 32px;

  & > p > strong {
    font-weight: 600;
  }
`;

const TagWrapper = styled.div`
  display: flex;
  margin-bottom: 20px;

  & > div {
    margin-right: 5px;
  }
`;

const ColumnDetail = () => {
  return (
    <Container>
      <Text>
        <p>
           .     ..      
           ,     .   
          .      .
        </p>
        <br />
        <p>
          <strong>      .</strong>
        </p>
        <br />
        <p>
                     .
                  .     
                    ..{' '}
        </p>
        <br />
        <p>
            1:1        ! 
                   !
        </p>
        <br />
        <p>
              1    .{' '}
          <strong>    .</strong>     .
          ()
        </p>
      </Text>
      <TagWrapper>
        <Tag text="#" border={COLORS.gray_01} textColor={COLORS.gray_04} />
        <Tag text="#" border={COLORS.gray_01} textColor={COLORS.gray_04} />
        <Tag text="#" border={COLORS.gray_01} textColor={COLORS.gray_04} />
        <Tag text="#" border={COLORS.gray_01} textColor={COLORS.gray_04} />
      </TagWrapper>
      <hr />
    </Container>
  );
};

export default ColumnDetail;
